import React, { useState } from "react";
import { Menu, Layout, Button, Drawer, Space } from "antd";
import { PhoneOutlined, MenuOutlined } from "@ant-design/icons";
import "./Navbar.css";
import logo from "./logo.svg";

const { Header } = Layout;

const Navbar = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const toggleDrawer = () => {
    setIsDrawerOpen(!isDrawerOpen);
  };

  const handleMenuClick = (key) => {
    const section = document.querySelector(key);
    if (section) {
      section.scrollIntoView({ behavior: "smooth" });
      setIsDrawerOpen(false);
    }
  };

  const menuItems = [
    {
      key: "#what-you-get",
      label: "Что вы получите",
      onClick: () => handleMenuClick("#what-you-get"),
    },
    {
      key: "#results",
      label: "Результаты",
      onClick: () => handleMenuClick("#results"),
    },
    {
      key: "#cooperation",
      label: "Сотрудничество",
      onClick: () => handleMenuClick("#cooperation"),
    },
    {
      key: "social",
      label: (
        <Space>
          <Button
            shape="circle"
            icon={<PhoneOutlined />}
            href="tel:+79199659825"
            className="social-button"
          />
          <Button
            shape="circle"
            className="social-button"
            href="https://t.me/investdrugDmitrievKirill"
            target="_blank"
          >
            <img
              src={`${process.env.PUBLIC_URL}/images/telegram.png`}
              alt="Telegram"
              className="telegram-icon"
            />
          </Button>
        </Space>
      ),
    },
  ];

  return (
    <Header className="navbar">
      <div className="navbar-logo">
        <img src={logo} alt="Логотип" className="logo-img" />
      </div>

      <Menu
        mode="horizontal"
        theme="dark"
        items={menuItems}
        className="navbar-menu"
        style={{ background: "transparent", borderBottom: "none" }}
        onClick={(e) => handleMenuClick(e.key)}
      />
      <Button
        className="navbar-toggle"
        type="text"
        icon={<MenuOutlined />}
        onClick={toggleDrawer}
      />
      <Drawer
        title="Меню"
        placement="right"
        closable={true}
        onClose={toggleDrawer}
        open={isDrawerOpen}
      >
        <Menu
          mode="vertical"
          items={menuItems}
          onClick={(e) => handleMenuClick(e.key)}
        />
      </Drawer>
    </Header>
  );
};

export default Navbar;
