import React, { useEffect, useState } from "react";
import "./Results.css";

const Results = () => {
  const [isVisible, setIsVisible] = useState(false);

  const baseColor = "#328873";

  const hexToRgba = (hex, alpha) => {
    const r = parseInt(hex.slice(1, 3), 16);
    const g = parseInt(hex.slice(3, 5), 16);
    const b = parseInt(hex.slice(5, 7), 16);
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
  };

  useEffect(() => {
    const handleScroll = () => {
      const section = document.getElementById("results");
      if (section) {
        const rect = section.getBoundingClientRect();
        if (rect.top < window.innerHeight * 0.75) {
          setIsVisible(true);
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section
      className={`results ${isVisible ? "visible" : "hidden"}`}
      id="results"
    >
      <h2>Результаты</h2>
      <div className="top-row">
        {[
          {
            year: "2021",
            deals: "43 сделки",
            profit: "29,6% средний доход",
          },
          {
            year: "2022",
            deals: "57 сделка",
            profit: "31,8% средний доход",
          },
          {
            year: "2023",
            deals: "54 сделки",
            profit: "30,4% средний доход",
          },
        ].map((item, index) => (
          <div
            key={index}
            className={`year-block ${isVisible ? "animate" : ""}`}
            style={{ transitionDelay: `${index * 0.2}s` }}
          >
            <div className="year-header">• {item.year} ГОД •</div>
            <div className="year-stats">
              <p>{item.deals}</p>
              <p>{item.profit}</p>
              <p>{item.investors}</p>
            </div>
          </div>
        ))}
      </div>

      <div className="bottom-row">
        <div
          className={`photo-container ${isVisible ? "animate" : ""}`}
          style={{ transitionDelay: "0.3s" }}
        >
          <img
            src={`${process.env.PUBLIC_URL}/images/img1.jpg`}
            alt="Игорь Поташов"
          />
        </div>

        <div className="stats-column">
          {[
            { number: "80", text: "Консультаций за 2024 год" },
            { number: "30,6%", text: "Средний доход инвестора" },
            { number: "61", text: "Совершённых сделок" },
            { number: "34", text: "Инвесторов работало со мной" },
          ].map((item, index) => (
            <div
              key={index}
              className={`stat-item ${isVisible ? "animate" : ""}`}
              style={{
                transitionDelay: `${0.4 + index * 0.2}s`,
                backgroundColor: hexToRgba(baseColor, 0.7 + index * 0.1),
              }}
            >
              <div className="stat-content">
                <div className="stat-number">{item.number}</div>
                <p className="stat-text">{item.text}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default Results;
