import React, { useEffect, useState } from "react";
import { Row, Col, Typography } from "antd";
import "./Footer.css";

const { Title, Text } = Typography;

const Footer = () => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const section = document.getElementById("footer");
      if (section) {
        const rect = section.getBoundingClientRect();
        if (rect.top < window.innerHeight * 0.8) {
          setIsVisible(true);
        }
      }
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll();
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <footer className="footer" id="footer">
      <Row
        gutter={[40, 40]}
        className={`footer-content ${isVisible ? "visible" : "hidden"}`}
        justify="space-between"
        align="middle"
      >
        <Col xs={24} md={10} className="footer-image">
          <div className={`photo-container ${isVisible ? "animate" : ""}`}>
            <img
              src={`${process.env.PUBLIC_URL}/images/img5.jpg`}
              alt="Игорь Поташов"
            />
          </div>
        </Col>

        <Col xs={24} md={14} className="footer-left">
          <Title level={2} className="footer-heading">
            Свяжитесь со мной,
            <br />и я помогу достичь ваших{" "}
            <span className="accent">финансовых целей</span>
          </Title>
          <div className="footer-contacts">
            <Text className="footer-label">КОНТАКТЫ</Text>
            <a
              href="mailto:kirill.dmitriev.job@mail.ru"
              className="footer-email"
            >
              kirill.dmitriev.job@mail.ru
            </a>
            <a href="tel:+79199659825" className="footer-phone">
              +7 (919) 965 98 25
            </a>
            <a
              href="https://t.me/investdrugDmitrievKirill"
              target="_blank"
              rel="noopener noreferrer"
              className="footer-telegram"
            >
              @investdrugDmitrievKirill
            </a>
          </div>
        </Col>
      </Row>
    </footer>
  );
};

export default Footer;
