import React, { useState } from "react";
import { Modal, Form, Input, message } from "antd";
import "./Hero.css";

const Hero = () => {
  const [showTooltip, setShowTooltip] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const [form] = Form.useForm();

  const handleSubmit = (values) => {
    console.log("Форма отправлена:", values);
    message.success("Заявка отправлена!");
    form.resetFields();
    setIsModalOpen(false);
  };

  return (
    <section className="hero" id="hero">
      <div className="hero-overlay"></div>
      <div className="hero-content">
        <h1 className="hero-title">
          <div className="hero-title-wrapper">
            Кирилл Дмитриев
            <span className="hero-plus-wrapper">
              <span
                className="hero-plus"
                onClick={() => setShowTooltip((prev) => !prev)}
                onMouseEnter={() =>
                  window.innerWidth > 768 && setShowTooltip(true)
                }
                onMouseLeave={() =>
                  window.innerWidth > 768 && setShowTooltip(false)
                }
              >
                +
              </span>
              {showTooltip && (
                <div className="hero-tooltip">
                  <ul>
                    <li>Первая инвестиционная сделка в 2020 году</li>
                    <li>Финансовый консультант уже более 3 лет</li>
                    <li>В активе у клиентов более 20 млн руб</li>
                    <li>Инвестор и партнер крупной инвестиционной платформы</li>
                    <li>
                      Автор нескольких стратегий автоследования в Т-инвестициях
                    </li>
                  </ul>
                </div>
              )}
            </span>
          </div>
        </h1>
        <p className="hero-description">
          Зарабатываю на инвестициях и помогу заработать вам
        </p>
        <button className="hero-button" onClick={() => setIsModalOpen(true)}>
          Связаться
        </button>
      </div>

      <Modal
        open={isModalOpen}
        footer={null}
        onCancel={() => setIsModalOpen(false)}
        centered
        className="custom-modal"
      >
        <div className="modal-content-wrapper">
          <img
            src={`${process.env.PUBLIC_URL}/images/img1.jpg`}
            alt="Игорь Поташов"
            className="modal-photo"
          />
          <div className="modal-form">
            <h2 className="modal-title">Буду рад сотрудничеству</h2>
            <p className="modal-subtext">
              Оставьте ваши контактные данные и я вам перезвоню в удобное время
            </p>

            <Form layout="vertical" form={form} onFinish={handleSubmit}>
              <Form.Item
                name="name"
                rules={[{ required: true, message: "Введите имя" }]}
              >
                <Input placeholder="Как вас зовут?" />
              </Form.Item>
              <Form.Item
                name="email"
                rules={[
                  { required: true, message: "Введите email" },
                  { type: "email", message: "Введите корректный email" },
                ]}
              >
                <Input placeholder="Ваш email" />
              </Form.Item>
              <Form.Item>
                <button type="submit" className="submit-button">
                  ОТПРАВИТЬ
                </button>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>
    </section>
  );
};

export default Hero;
